@mixin grid-layout {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(5, 1fr);
    column-gap: 48px;
    row-gap: 16px;
    width: 100%;
    height: 100%;
    justify-items: center;
    align-items: start;
